.related-tracks {
    height: 100%;
    max-height: 100vh;
}

.music-title {
    position: relative;
    top: -10px;
    overflow: hidden;
    line-height: 100% !important;
    font-size: 1em !important;
    color: white;
}
.grid-container {
    height: auto !important;
    width: 100%;
    position: relative;
    top: 20px;
    overflow: hidden;
    left: 0.5vh;
}
.shade {
    background: rgba(0,0,0,.8);
    height: inherit;
    width: 100%;
    z-index: 10;
    position: relative;
}

.dist-box-tracks {
        position: relative;
    left: 4vh;
    margin-right: 2vh;
}
.track-card {
    height: 28vh;
    margin-bottom: 2vh;
    width: 95% !important;
    max-width: 35vh;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2) !important;
}
.track-card:hover, .track-card:active {
    opacity: 0.8;
    cursor: pointer;
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2) !important;

}
.track-info {
    line-height: 10%;
    overflow: hidden;
    height: 10%;
    background: #272c34 !important;
}
.track-img {
    height: 100%;
    width: 100%;
}


.play-btn {
    position: relative;
    top: 40%;
    opacity: 1 !important;



}



.track-tab {
    border-top: none !important;
    border-left: none !important;
    border-right: none !important;
    outline: none !important;
    color: rgba(0,0,0,0.75) !important;
    font-weight: 600 !important;
    font-size: 0.9em !important;
}
.track-tab:active {
    border-top: none !important;
    border-left: none !important;
    border-right: none !important;
    outline: none !important;
}
.browser-container {
    position: relative;
    left: 6vh;
    margin-right: 8vh;
    marginTop: 64px;
}